<template>
  <div class="wounds pt-5">
    <div class="container-fluid">

      <div class="row justify-content-center ml-1 bg-block">
        <h2 class="py-2">{{title}}</h2>
        <div class="d-flex">
          <div class="row justify-content-center px-3 ">
            <div class="col-12 col-md-4">
              <div class="block">
                <p>{{startingWoundsText}}
                </p>
                <input v-model="startingWounds" type="number">
              </div>
            </div>
            <div class="col-12 col-md-8">
              <div class="block"><p>{{subTitle}}
              </p>
                <p>{{ currentWounds }}</p>
                <button class="add" v-if="currentWounds < startingWounds"  @click="addnumbers(1)">+ 1</button>
                <button class="add" v-if="currentWounds < startingWounds" @click="currentWounds += 5">+ 5</button>
                <button class="minus" v-if="currentWounds > 0" @click="subnumber(1)">- 1</button>
                <button class="minus" v-if="currentWounds > 0" @click="subnumber(5)">- 5</button>
              </div>
              <button class="update" @click="persist3">Update</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      msg: String
    },
    data() {
      return {
        startingWounds: 0,
        title: 'Wounds',
        subTitle:'Current Wounds',
        startingWoundsText: 'Starting Wounds',
        currentWounds:0
      }
    },
    mounted() {
      if (localStorage.startingWounds) {
        this.startingWounds = localStorage.startingWounds;
      }
      if (localStorage.currentWounds) {
        this.currentWounds = localStorage.currentWounds;
      }
    },
    watch: {
      name(newWound) {
        localStorage.startingWounds = newWound;
      }
    },
    methods:{
      persist3(){
        localStorage.startingWounds = this.startingWounds;
        this.alertDisplay();
      },
      alertDisplay() {
        this.$swal( '','Success');
      },
      addnumbers(inc){
        this.currentWounds += parseInt(inc);

      },
      subnumber(dec){
        this.currentWounds -= parseInt(dec);
      },
    }

  }
</script>

