<template>
  <div class="note my-5">
    <div class="container-fluid">
      <div class="row justify-content-center ml-1 bg-block pb-4">
        <h2 class="py-2">{{title}}</h2>
        <div class="d-flex">
          <div class="row justify-content-center px-3 ">
            <div class="col-12">
              <div class="block-note">
                <ul>
                  <li v-for="(notes, n) in note" v-bind:key="notes.id" > {{notes}}   <button @click="removenote(n)"><svg xmlns="http://www.w3.org/2000/svg" fill="red" width="25" height="25" viewBox="0 0 24 24"><path d="M12 0c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm6 13h-12v-2h12v2z"/></svg></button></li>
                </ul>
              </div>
            </div>
            <div class="col-12">
              <div class="block-addnote">
                <textarea v-model="newnote"></textarea>
                <button @click="addnote"><svg xmlns="http://www.w3.org/2000/svg" fill="green" width="25" height="25" viewBox="0 0 24 24"><path d="M12 0c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm6 13h-5v5h-2v-5h-5v-2h5v-5h2v5h5v2z"/></svg></button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      msg: String
    },
    data() {
      return {
        note:[],
        newnote:null,
        title: 'Notes'
      }
    },
    mounted() {
      if (localStorage.getItem('note')) {
        try {
          this.note = JSON.parse(localStorage.getItem('note'));
        } catch(e) {
          localStorage.removeItem('note');
        }
      }
    },
    methods: {
      addnote() {
        // ensure they actually typed something
        if (!this.newnote) {
          return;
        }

        this.note.push(this.newnote);
        this.newnote = '';
        this.savenote();
      },
      alertDisplay() {
        this.$swal( '','Success');
      },
      removenote(x) {
        this.note.splice(x, 1);
        this.savenote();
      },
      savenote() {
        const parsed = JSON.stringify(this.note);
        localStorage.setItem('note', parsed);
        this.alertDisplay();
      }
    }
  }
</script>

