<template>

  <div class="gold mt-5">
    <div class="container-fluid">
      <div class="row justify-content-center ml-1 bg-block">
        <h2 class="py-2">{{title}}</h2>
        <div class="d-flex">
          <div class="row justify-content-center px-3 ">
            <div class="col-12 col-md-4">
              <div class="block-gold">
                <p>
                  {{totalAmount}}
                </p>

              </div>
            </div>
            <div class="col-12 col-md-8">
              <div class="block "><p>{{subTitle}}
              </p>
                <p>{{ amountText }}</p>
                <input  class="current-wounds" type="number"  v-model="amount" min="1">
                <button class="add"  @click="addnumbers"><svg xmlns="http://www.w3.org/2000/svg" fill="green" width="24" height="24" viewBox="0 0 24 24"><path d="M12 0c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm6 13h-5v5h-2v-5h-5v-2h5v-5h2v5h5v2z"/></svg></button>
                <button class="minus" @click="subnumber"><svg xmlns="http://www.w3.org/2000/svg" fill="red" width="24" height="24" viewBox="0 0 24 24"><path d="M12 0c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm6 13h-12v-2h12v2z"/></svg></button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      msg: String
    },
    data() {
      return {
        totalAmount: 0,
        title: 'Gold',
        subTitle:'Add or remove Gold',
        amountText: 'Amount',
        amount:0,
        totalAmountText:'Total',
        popupActivo:false

      }
    },
    mounted() {
      if (localStorage.total) {
        this.totalAmount = localStorage.total;
      }
    },
    watch: {
      name(newTotal) {
        localStorage.total = newTotal;
      }
    },
    methods:{
      persist4(){
        localStorage.total = this.totalAmount;
      },
      alertDisplay() {
        this.$swal( '','Success');
      },
      addnumbers(){
        this.totalAmount =  parseInt(this.totalAmount) + parseInt(this.amount);
        this.persist4();
        this.alertDisplay();
      },
      subnumber(){
        this.totalAmount =  parseInt(this.totalAmount) -  parseInt(this.amount);
        this.persist4();
        this.alertDisplay();
      },

    }

  }
</script>

