<template>
  <div class="skill my-5">
    <div class="container-fluid">
      <div class="row justify-content-center ml-1 bg-block pb-4">
        <h2 class="py-2">{{title}}</h2>
        <div class="d-flex">
          <div class="row justify-content-center px-3 ">
            <div class="col-12">
              <div class="block-skill">
                <ul>
                  <li v-for="(skills, n) in skill" v-bind:key="skills.id" > {{skills}}   <button @click="removeSkill(n)"><svg xmlns="http://www.w3.org/2000/svg" fill="red" width="25" height="25" viewBox="0 0 24 24"><path d="M12 0c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm6 13h-12v-2h12v2z"/></svg></button></li>
                </ul>
              </div>
            </div>
            <div class="col-12">
              <div class="block-addskill">
                <input v-model="newSkill">
                <button @click="addSkill"><svg xmlns="http://www.w3.org/2000/svg" fill="green" width="25" height="25" viewBox="0 0 24 24"><path d="M12 0c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm6 13h-5v5h-2v-5h-5v-2h5v-5h2v5h5v2z"/></svg></button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      msg: String
    },
    data() {
      return {
        skill:[],
        newSkill:null,
        title: 'Skills / Spells'
      }
    },
    mounted() {
      if (localStorage.getItem('skill')) {
        try {
          this.skill = JSON.parse(localStorage.getItem('skill'));
        } catch(e) {
          localStorage.removeItem('skill');
        }
      }
    },
    methods: {
      addSkill() {
        // ensure they actually typed something
        if (!this.newSkill) {
          return;
        }

        this.skill.push(this.newSkill);
        this.newSkill = '';
        this.saveSkill();
      },
      alertDisplay() {
        this.$swal( '','Success');
      },
      removeSkill(x) {
        this.skill.splice(x, 1);
        this.saveSkill();
      },
      saveSkill() {
        const parsed = JSON.stringify(this.skill);
        localStorage.setItem('skill', parsed);
        this.alertDisplay();
      }
    }
  }
</script>

