<template>
    <div class="profile">
        <div class="container-fluid">

            <div class="row justify-content-center ml-1 bg-block">
                <h2 class="py-2">{{title}}</h2>
                <div class="d-flex">
                    <div class="row justify-content-center px-3 ">
                        <div class="col-6 col-lg-3">
                            <div class="block">
                                <p>{{textName}}
                                </p>
                                <input v-model="name">
                            </div>
                        </div>
                        <div class="col-6 col-lg-3">

                            <div class="block"><p>{{textLevel}}
                            </p>
                                <input type="number" min="1" max="10" v-model="battleLevel">
                            </div>
                        </div>
                        <div class="col-6 col-lg-3">
                            <div class="block">
                                <p>{{textRace}}
                                </p>
                                <input v-model="race" v-on:keyup.enter="textRace">
                            </div>
                        </div>
                        <div class="col-6 col-lg-3">
                            <div class="block">
                                <p>{{textType}}
                                </p>
                                <input v-model="type">
                            </div>
                        </div>
                    <button class="update" @click="persist">Update</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            msg: String
        },
        data() {
            return {
                name: '',
                battleLevel: '',
                race: '',
                type: '',
                textName: 'Name:',
                textRace: 'Race:',
                textType: 'Warrior Type:',
                textLevel: 'Battle Level:',
                title: 'Profile'
            }
        },
        mounted() {
            if (localStorage.name) {
                this.name = localStorage.name
            }
            if (localStorage.battleLevel) {
                this.battleLevel = localStorage.battleLevel
            }
            if (localStorage.race) {
                this.race = localStorage.race
            }
            if (localStorage.type) {
                this.type = localStorage.type
            }


        },
        methods: {
            persist() {
                localStorage.name = this.name;
                localStorage.battleLevel = this.battleLevel;
                localStorage.race = this.race;
                localStorage.type = this.type;
                this.alertDisplay()
            },
            alertDisplay() {
                this.$swal( '','Success');
            },
        }
    }
</script>

